//获取验证码
const getMsgCode = (reqParam) => {
    return new Promise((resolve, reject) => {
        http.post('hisense-systemServer/login/getMsgCode.do', { data: JSON.stringify(reqParam) }, true ,'homeUrl').then(result => {
            if (result.success == true) {
                resolve(result)
            }else{
                Toast(result.msg)
                reject(result.msg)
            }
            // result.success == true ? resolve(result) : reject(result.msg);
        }).catch(error => {
            Toast('网络异常，请稍后重试！')
            reject(error);
        })
    });
}