<template>
  <div style="height:100%;background:#FFFFFF;width:100%;">
    <page-header @btn-action="openApp"/>
    <div class="contentDetail">
      <div style="margin-bottom:40px;">
        <div class="title">验证手机号</div>
        <span style="font-size:16px;">通过验证后审核结果立即生效</span>
      </div>
      <div>
        <div style="font-size:16px;font-weight:bold">请填写您在物业预留的产权人手机号</div>
        <van-divider />

        <van-row type="flex" justify="space-between" @click="showKeyBoardAction">
          <van-col
            class="phoneItem"
            span="2"
            v-for="i in 11"
            :key="i"
          >{{phoneDatas.length >= i ? phoneDatas[i-1] : ''}}</van-col>
        </van-row>
      </div>
      <div class="bottomView">
        <div class="nextBtn" @click="nextSetp">下一步</div>
      </div>
      <div class="tipView">
        <span class="tips" @click="pusToChangeGuide">我是产权人,可是我换手机号了</span>
      </div>
      <!-- 数字键盘 -->
      <van-number-keyboard
        :show="showKeyboard"
        @input="onInput"
        @delete="onDelete"
        @blur="showKeyboard = false"
      />
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Divider } from "vant";
import { NumberKeyboard } from "vant";
import { Row, Col } from "vant";
import PageHeader from "./PageHeader"

Vue.use(Row).use(Col);
Vue.use(NumberKeyboard);
Vue.use(Divider);
import { verifyPhone } from "../../service/verifyApplyService";
export default {
  name: "VerifyPhone",
  components: {
    PageHeader
  },
  data() {
    return {
      showKeyboard: false,
      phoneDatas: [],
      icon: {
        xwjlogo: require("../../assets/share/shareLogo.png")
      }
    };
  },
  methods: {
    showKeyBoardAction() {
      if (this.showKeyboard) return;
      this.showKeyboard = true;
    },
    onInput(key) {
      console.log(key);
      if (this.phoneDatas.length < 11) {
        this.phoneDatas.push(key);
      }
    },
    onDelete() {
      if (this.phoneDatas.length > 0) {
        this.phoneDatas.pop();
      }
    },
    nextSetp() {
      if (!this.btnEnable) {
        this.$toast("请输入正确的手机号");
        // this.$router.push("commitPage");
        return;
      }
      this.verifyPhoneNum();
    },
    verifyPhoneNum() {
      let dataObj = JSON.parse(this.$route.query.data);
      let param = {
        uuid: dataObj.uuid,
        phone: this.phone
      };
      verifyPhone(param)
        .then(data => {
          this.$router.push({
            name: "commitPage",
            query: {
              data: this.$route.query.data,
              type: this.$route.query.type,
              phone: this.phone
            }
          });
        })
        .catch(error => {
          // this.$toast(error);
          console.log(error);
        });
    },
    pusToChangeGuide() {
      this.$router.push("changeGuide");
    },
    openApp() {
      this.shareOpenApp('myfamily')
    }
  },
  mounted() {
    console.log(this.$route.query);
    console.log(JSON.parse(this.$route.query.data).uuid);
    
    this.androidShareOpenApp('myfamily')//android 拉起APP
  },
  computed: {
    phone() {
      let phoneNum = "";
      for (let num of this.phoneDatas) {
        phoneNum += num;
      }
      return phoneNum;
    },
    btnEnable() {
      return this.phoneDatas.length == 11;
    }
  }
};
</script>

